<template>
  <!---->
  <md-card class="md-elevation-24" md-with-hover>
    <img src="/static/config/register.png"/>
    <md-card-content>
      <!---->
      <div class="md-title" style="text-align: left"></div>

      <!---->
      <md-field :class="isPhone(form.phone_number)">
        <md-icon>phone</md-icon>
        <label></label>
        <md-input  v-model="form.phone_number">
        </md-input>
        <md-button></md-button>
        <span class="md-error"></span>
      </md-field>

      <!---->
      <md-field>
        <md-icon>bookmark</md-icon>
        <label></label>
        <md-input>
        </md-input>
      </md-field>

      <!---->
      <md-field :class="$myapi.isPassword(form.password)">
        <md-icon>lock</md-icon>
        <label></label>
        <md-input type="password" v-model="form.password">
        </md-input>
        <span class="md-error" v-if="form.password">6</span>
      </md-field>

    </md-card-content>

    <!---->
    <md-card-actions>
      <md-button to="/login"><md-icon>help</md-icon>
      </md-button>
      <md-button class="md-raised md-primary" v-on:click="register"><md-icon>done</md-icon>
      </md-button>
    </md-card-actions>

  </md-card>
</template>

<script>
  export default {
    name: "Register",
    data() {
      return {
        // 
        words: ['<b style="color:orange">~~~</b>'],
        // 
        form: {
          password: '',
          phoneNumber: '',
        },
      }
    },
    methods: {
      isPhone(phone_number) {
        if (phone_number)
          return this.$myapi.isPhone(phone_number) ? '' : 'md-invalid'
        else return ''
      },
      isNull(password) {
        if (password)
          return this.$myapi.isPassword(password) ? '' : 'md-invalid'
        else return ''
      },
      register() {
        let _this = this;
        if (_this.$myapi.isPhone(_this.form.phone_number) && !_this.$myapi.isPassword(_this.form.password)) {
          this.$myapi.post('/user/add', _this.form, function (res) {
            _this.$alert.show(100, "+")
            sessionStorage.setItem("token", res.token)
            _this.$router.push("/register-more")
          })
        } else {
          _this.$alert.show("999", "")
        }
      }
    },
  }
</script>

<style scoped>
  .my-hover:hover {
    cursor: url('/static/mouse/mouse2.cur'), auto !important;
    background-color: rgba(255, 255, 255, .8);
  }

  .my-raised {
    background-color: rgba(10, 10, 0, .1);
    border-radius: 7px;
  }
</style>



