<template>

  <md-card class="md-elevation-24" md-with-hover>

    <div class="md-layout md-gutter md-alignment-top-center">
      <div class="md-layout-item md-size-40 md-large-size-40  md-medium-size-40 md-small-size-40 md-xsmall-size-40">
        <md-card-content>
          <img :src="user_info.photoUrl" class="want " id="head_img" style="width: 100%;text-align: left"/>
          <p>=</p>
        </md-card-content>
      </div>


      <div class="md-layout-item md-size-60 md-large-size-60 md-medium-size-60 md-small-size-100 md-xsmall-size-100"
           v-if="$store.state.isLogin">
        <md-card-content>
          <p>{{user_info.userName}}</p>
          <p>{{user_info.sex==0?'':''}}</p>
          <p>{{user_info.autograph}}</p>
          <p>{{user_info.age}}</p>
          <p>{{myMoney}}</p>
        </md-card-content>
        <md-card-actions>
          <md-button  to="/edit"></md-button>
          <md-button  to="/mymoney"></md-button>
        </md-card-actions>
      </div>
      <div v-else>
        <p style="color: rgba(0,0,0,1);font-size: 25px">...</p>
        <p style="color: rgba(0,0,0,1);font-size: 23px">...</p>
        <p style="color: rgba(0,0,0,1);font-size: 20px;"></p>
        <p style="color: rgba(0,0,0,1);font-size: 17px;"><span style="color: aquamarine;text-decoration-line: underline" v-on:click="$router.push('/login')"></span></p>
        <p style="color: rgba(0,0,0,1);font-size: 14px;">......<span style="color: red;font-size: 10px"></span></p>
        <p style="color: rgba(0,0,0,1);font-size: 10px;">......</p>
        <p style="color: rgba(0,0,0,1);font-size: 7px;">......</p>
        <p style="color: rgba(0,0,0,1);font-size: 5px;">der......</p>
        <p style="color: rgba(0,0,0,1);font-size: 2px;">......</p>

      </div>

    </div>
  </md-card>
</template>

<script>
  export default {
    name: "UserCard",
    data() {
      return {
        user_info: {
          age: "",
          autograph: "",
          bankCardNumber: null,
          identityCard: null,
          name: null,
          photoUrl: '/static/default.jpg',
          questionResult: null,
          sex: null,
          showMe: "",
          userId: null,
          userName: '',
        },
        myMoney: 0
      }
    },
    methods: {
      getMe() {
        let _this = this
        this.$myapi.get("/user_info/me", null, function (res) {
          _this.user_info = res.data
          _this.$store.commit('setUserInfo',res.data)
        })
      },
      logout() {
        sessionStorage.clear()
        this.user_info.photoUrl = '/static/default.jpg'
        this.$store.commit("logout")
      },
      show_buy_amount() {
        let _this = this
        _this.$loading.show()
        this.$myapi.get("/buy_order/show/buy_amount", null, function (res) {
          _this.myMoney = res.data.getAllAmount
          _this.$loading.hide()
        })
      }
    },
    created() {
      this.$store.commit("loginByToken",sessionStorage.hasOwnProperty("token"))
      if (this.$store.state.isLogin) {
        this.getMe()
        this.show_buy_amount()
      }
    }
  }

</script>

<style scoped>

</style>



